* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none;
}

html {
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
}

.decor_block {
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10px;
  margin: 40px auto;
}

.decor_block_line {
  display: block;
  background: #fff;
  width: 80px;
  height: 2px;
}

.decor_block_dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #e74e3e;
}

header {
  position: relative;
}

.header__logo {
  cursor: pointer;
}

.header_container {
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  z-index: 1;
  padding-top: 40px;
}

.header_nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.3s;
}

.header__menu {
  display: flex;
}

.header__menu_link {
  font-size: 14px;
  color: #fff;
  margin-left: 60px;
  transition: 0.3s;
  position: relative;
  outline: none;
}
.header__menu_link::before {
  content: "";
  position: absolute;
  bottom: -22px;
  width: 0%;
  height: 2px;
  background: #e74e3e;
  transition: 0.3s;
}
.header__menu_link:hover::before, .header__menu_link:focus::before {
  width: 100%;
}
.header__menu_link:hover, .header__menu_link:focus {
  color: #e74e3e;
}

.header__burger {
  width: 40px;
  height: 40px;
  cursor: pointer;
  display: none;
}

.slider {
  position: relative;
  top: 0;
  z-index: 0;
  clip-path: polygon(0 0, 100% 0, 100% 90%, 50% 100%, 50% 100%, 0 90%);
  transition: 0.3s;
}

.slider__track {
  display: flex;
  width: 100%;
  transition: 0.3s;
  height: 100%;
  height: 990px;
}

.slide {
  z-index: 1;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  background-position: center;
  background-size: cover;
}

.slide_first {
  background-image: url("../img/header/slide-bg-1.png");
}

.slide_second {
  background-image: url("../img/header/slide-bg-2.jpg");
}

.slide_third {
  background-image: url("../img/header/slide-bg-3.jpg");
}

.slide_inner {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.65;
}

.slide__content {
  position: relative;
  color: #f4f5f9;
  text-align: center;
}

.slide__top_header {
  font-size: 24px;
}

.slide__main_header {
  margin-top: 45px;
  font-size: 72px;
}

.slide__text_block {
  max-width: 580px;
  margin: 0 auto;
  font-size: 14px;
  line-height: 22px;
}

.slide__handle_block {
  margin-top: 70px;
}

.slide__btn {
  border: 1px solid #fff;
  display: inline-block;
  border-radius: 3px;
  background: none;
  font-size: 14px;
  color: #fff;
  width: 225px;
  padding: 20px 0;
  margin: 0 15px;
  outline: none;
  transition: 0.3s;
}
.slide__btn:hover, .slide__btn:focus {
  background: #e74e3e;
  border: 1px solid #e74e3e;
}

.sldier_btn {
  background: #716a64;
  border: none;
  font-size: 25px;
  width: 45px;
  height: 45px;
  position: absolute;
  top: 50%;
  z-index: 2;
  border-radius: 2px;
  transition: 0.3s;
}
.sldier_btn:hover {
  background: #e74e3e;
  color: #fff;
}

.sldier_btn--next {
  right: 1.5%;
}

.sldier_btn--prev {
  left: 1.5%;
}

.anchor_container {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 100px;
  outline: none;
  z-index: 0;
}

.anchor_container__img_box {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  bottom: -25px;
  left: 0;
  right: 0;
  margin: 0 auto;
  border-radius: 50%;
  transition: 1s;
}
.anchor_container__img_box::after {
  content: "";
  width: 100px;
  height: 100px;
  border-radius: 100%;
  position: absolute;
  opacity: 0.3;
  z-index: 5;
  transition: 0.3s;
  animation: anchor_animation 1.5s alternate forwards infinite;
}

.header__anchor {
  z-index: 6;
}

@keyframes anchor_animation {
  0% {
    background: #fff;
    width: 70px;
    height: 70px;
  }
  100% {
    background: #fff;
    width: 90px;
    height: 90px;
  }
}
.advantages_container {
  margin-top: 130px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 110px;
}

.advantage {
  text-align: center;
}
.advantage:hover > .advantage__header_block > .advantage__header__img_block::after {
  transform: rotate(45deg);
  border: 1px solid #e74e3e;
}
.advantage:hover > .advantage__header_block > .advantage__header {
  color: #e74e3e;
}

.advantage__header__img_block {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border-radius: 2px;
  position: relative;
}
.advantage__header__img_block::after {
  content: "";
  position: absolute;
  width: 70px;
  height: 70px;
  top: 0;
  right: 0;
  left: 0;
  border: 1px solid #999999;
  border-radius: 2px;
  transition: 0.3s;
}

.advantage__header {
  font-size: 18px;
  color: #6a6a6a;
  margin: 30px 0 20px 0;
  transition: 0.3s;
}

.advantage__decor {
  height: 1px;
  width: 40px;
  background: #6a6a6a;
  margin: 0 auto;
}

.advantage__text {
  margin-top: 20px;
  max-width: 250px;
  font-size: 12px;
  color: #999999;
}

.services_wrapper {
  background: #202020;
  display: flex;
  color: #fff;
  text-align: right;
}

.services__content_block {
  width: 54%;
  padding: 150px 0;
}

.services__header {
  font-size: 36px;
  display: flex;
  justify-content: end;
  align-items: center;
  margin-right: 80px;
}

.services__header_decor {
  height: 36px;
  display: inline-block;
  width: 6px;
  background: #e74e3e;
  margin-left: 30px;
}

.service {
  display: flex;
  align-items: center;
  justify-content: end;
  margin-top: 60px;
}

.service__header {
  font-size: 18px;
  color: #e74e3e;
  font-weight: 400;
}

.service__text {
  color: #999999;
  font-size: 14px;
  max-width: 565px;
  line-height: 28px;
}

.service__img {
  padding-left: 25px;
  transform: translate(30px);
  transition: 0.2s;
}

.services__image_block {
  height: auto;
  width: 46%;
  background-image: url("../img/main/services_bg.png");
  background-size: cover;
}

.feedback_wrapper {
  background: #f4f5f9;
}

.feedback_container {
  padding: 150px 0;
}

.feedback__header_block {
  text-align: center;
}

.feedback__header {
  color: #6a6a6a;
  font-size: 36px;
}

.feedback__sub_header {
  margin: 25px auto;
  color: #999999;
}

.feedback__decor_block {
  margin: 0 auto;
}

.feedback__decor_block_line {
  background: #999999;
}

.feedback__form_block {
  margin-top: 70px;
  display: flex;
  justify-content: space-between;
}

.feedback__info_block {
  max-width: 480px;
  max-height: 570px;
  box-shadow: 0px 6px 8px 0px rgba(34, 60, 80, 0.2);
  background: #fff;
  padding-top: 100px;
  padding: 100px 120px 100px 90px;
  padding-left: 90px;
  color: #999999;
  font-size: 18px;
}

.feedback__info_block__header {
  font-size: 24px;
  color: #e74e3e;
}

.feedback__calls {
  margin: 65px auto;
}

.feedback__form {
  box-shadow: 0px 6px 8px 0px rgba(34, 60, 80, 0.2);
  background: #fff;
  padding: 100px;
  display: flex;
  flex-direction: column;
  color: #999999;
  max-width: 670px;
  max-height: 570px;
}

.feedback__form_input {
  outline: none;
  border: none;
  border-bottom: 1px solid #e5e5e5;
  resize: none;
  margin-bottom: 20px;
  width: 460px;
  font-size: 16px;
  transition: 0.3s;
}

.invalid_data {
  border-bottom: 1px solid #e74e3e;
}

.feedback__send_btn {
  font-size: 14px;
  border: none;
  border-radius: 3px;
  background: #e74e3e;
  width: 160px;
  padding: 15px 20px;
  color: #fff;
  font-weight: 600;
  transition: 0.3s;
  cursor: pointer;
  position: relative;
  transition: 0.3s;
  outline: none;
}
.feedback__send_btn::after {
  content: "";
  position: absolute;
  width: 99%;
  height: 98%;
  top: 0;
  right: 0;
  left: 0;
  border: 1px solid #e74e3e;
  border-radius: 3px;
  visibility: hidden;
  transition: 0.2s;
}
.feedback__send_btn:hover::after, .feedback__send_btn:focus::after, .feedback__send_btn:active::after {
  visibility: visible;
}
.feedback__send_btn:focus, .feedback__send_btn:hover, .feedback__send_btn:active {
  background: #fff;
  color: #e74e3e;
}

.promo_container {
  text-align: center;
  color: #fff;
  background: #000;
  padding: 150px 0 140px 0;
  position: relative;
  background-image: url("../img/footer/promo.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.promot__shadow_block {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #000;
  opacity: 0.55;
  z-index: 0;
}

.promo__inner {
  position: relative;
  z-index: 2;
}

.promo__header {
  font-size: 36px;
}

.promo__text {
  font-size: 18px;
  margin: 10px 0 60px 0;
}

.promo__btn {
  font-size: 14px;
  background: #e74e3e;
  text-transform: uppercase;
  border: none;
  color: #fff;
  max-width: 225px;
  border-radius: 3px;
  padding: 20px 47px;
  cursor: pointer;
  position: relative;
  outline: none;
  transition: 0.3s;
}
.promo__btn::after {
  content: "";
  position: absolute;
  width: 99%;
  height: 98%;
  top: 0;
  right: 0;
  left: 0;
  border: 1px solid #fff;
  border-radius: 3px;
  visibility: hidden;
  opacity: 0;
  transition: 0.2s;
}
.promo__btn:hover::after, .promo__btn:focus::after, .promo__btn:active::after {
  visibility: visible;
  transition: 0.3s;
  opacity: 1;
}

.footer_wrapper {
  background: #202020;
  position: relative;
}

.footer__anchor {
  position: absolute;
  right: 50px;
  bottom: 55px;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background: #e74e3e;
  color: #fff;
  font-size: 25px;
  border: none;
  transition: 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__anchor:hover {
  background: #fff;
  color: #e74e3e;
}

.footer_container {
  display: flex;
  justify-content: space-between;
  padding: 30px 0;
  color: #999999;
  font-size: 14px;
}

.footer__menu {
  display: flex;
}

.footer__menu_link {
  margin-right: 20px;
}
.footer__menu_link svg > path {
  transition: 0.3s;
  background: #000;
  fill-rule: evenodd;
  fill: #6a6a6a;
}
.footer__menu_link:hover > svg path {
  fill: #e74e3e;
}

.applications_container {
  min-height: 500px;
}

.search_block {
  margin: 0 auto;
  text-align: center;
}

.applications_block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#application__delete_btn {
  margin-top: 10px;
  border: none;
  background: #e74e3e;
  color: #fff;
  padding: 10px 20px;
  transition: 0.3s;
  border-radius: 3px;
}
#application__delete_btn:hover {
  color: #e74e3e;
  background: #fff;
}

.applications_input {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  padding: 10px 0;
  font-size: 20px;
  margin: 20px;
}

.application {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 500px;
  padding: 50px;
  background: rgb(66, 65, 68);
  color: #fff;
  margin: 20px auto;
  font-size: 18px;
}

.back {
  background: #202020;
  border: 1px solid #6a6a6a;
  font-size: 20px;
  padding: 10px 15px;
  display: block;
  max-width: 120px;
  margin: 0 auto;
  border-radius: 3px;
  color: #fff;
}

@media (max-width: 1440px) {
  .slider {
    clip-path: polygon(0 0, 100% 0, 100% 93%, 50% 100%, 50% 100%, 0 93%);
  }

  .footer__anchor {
    bottom: 70px;
  }
}
@media (max-width: 1280px) {
  .header_nav {
    padding: 0 10px;
  }

  .advantages_container {
    padding: 0 10px 150px;
  }

  .feedback__form_block {
    padding: 0 10px;
  }

  .feedback__form_input {
    width: 100%;
  }

  .footer_container {
    padding: 30px 10px;
  }
}
@media (max-width: 1024px) {
  .header_nav {
    position: relative;
  }

  .header__menu_block {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    display: none;
    transition: 0.5s;
  }

  .header__menu {
    flex-direction: column;
    z-index: 20;
  }
  .header__menu li {
    margin: 10px 0;
  }

  .header__menu_link {
    font-size: 35px;
    margin: 20px auto;
  }
  .header__menu_link::before {
    display: none;
  }

  .header__burger {
    display: block;
  }

  @keyframes show_menu_anim {
    0% {
      display: flex;
      visibility: hidden;
      opacity: 0;
    }
    100% {
      visibility: visible;
      opacity: 1;
    }
  }
  @keyframes hide_menu_anim {
    0% {
      visibility: visible;
      opacity: 1;
    }
    100% {
      visibility: hidden;
      opacity: 0;
      display: none;
    }
  }
  .show_menu {
    display: flex;
    animation: show_menu_anim forwards 0.5s;
  }

  .hide_menu {
    display: flex;
    animation: hide_menu_anim forwards 0.5s;
  }

  .sticky {
    background: #6a6a6a;
    padding: 5px 10px;
    width: 100%;
    position: fixed;
    top: 0;
  }

  .services__content_block {
    padding: 30px 10px;
  }

  .feedback__form_block {
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .feedback__info_block {
    min-width: 500px;
    margin-bottom: 20px;
  }

  .feedback__form {
    width: 500px;
  }
}
@media (max-width: 980px) {
  .slider {
    clip-path: polygon(0 0, 100% 0, 100% 95.5%, 50% 100%, 50% 100%, 0 95.5%);
  }

  .advantages_container {
    flex-wrap: wrap;
  }

  .advantage {
    margin-bottom: 30px;
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media (max-width: 780px) {
  .services__content_block {
    text-align: left;
    width: 100%;
  }

  .services__image_block {
    display: none;
  }

  .services__header {
    justify-content: center;
    margin: 0;
  }

  .service__img {
    transform: none;
  }
}
@media (max-width: 620px) {
  .slider__track {
    height: 800px;
  }

  .slide__main_header {
    font-size: 45px;
  }

  .slide__text {
    padding: 5px;
  }

  .sldier_btn {
    top: auto;
    bottom: 10%;
  }

  .slide__btn {
    width: 160px;
  }

  .advantage__text {
    padding: 0 5px;
  }

  .feedback__info_block {
    padding: 80px;
    height: 570px;
    min-width: auto;
    width: 100%;
    max-width: none;
  }

  .feedback__form {
    width: 100%;
    padding: 70px;
    height: 570px;
  }

  .promo__inner {
    padding: 0 5px;
  }

  .promo__header {
    font-size: 28px;
  }

  .footer_container {
    flex-direction: column;
    align-items: center;
  }

  .footer__anchor {
    right: 15px;
  }

  .footer__menu {
    margin-bottom: 10px;
  }
}
@media (max-width: 460px) {
  .feedback__info_block {
    padding: 70px 40px;
  }

  .feedback__form {
    padding: 70px 40px;
  }

  .footer__menu_link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 10px;
  }
}
@media (max-width: 400px) {
  .header__menu_link {
    font-size: 25px;
  }

  .slide__header_block {
    margin-top: 40px;
  }

  .slide__btn {
    display: block;
    width: 210px;
    margin: 10px auto;
  }

  .promo__header {
    font-size: 24px;
  }

  .footer__anchor {
    bottom: 90px;
  }
}
@media (max-width: 355px) {
  .slide__top_header {
    font-size: 22px;
  }

  .slide__main_header {
    font-size: 28px;
  }

  .sldier_btn {
    height: 35px;
    width: 35px;
  }

  .advantage {
    flex-basis: 100%;
  }

  .services__header {
    font-size: 26px;
  }

  .feedback__form,
.feedback__info_block {
    padding: 70px 20px;
  }

  .feedback__send_btn {
    margin: 0 auto;
  }

  .promo__text {
    font-size: 15px;
  }
}